.about-text {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 16px;
    font-weight: 300;
    line-height: 22px;
    margin: 20px auto;
    width: 60%;
}

#about-resume {
    color: #222;
    background: none;
    border: none;
    font-family: 'Lora', serif;
    font-size: 16px;
    margin-bottom: 10px;
    margin-left: 35.6%;
    margin-top: 10px;
    width: 30%;
}

#about-resume:hover {
    color: #777;
}

#about-resume:focus {
    outline: 0;
}

@media screen and (max-width: 700px) {
    .about-text {
        color: #000;
        font-size: 16px;
        line-height: 24px;
        width: 80%;
    }

    #about-resume {
        font-size: 18px;
    }
}


