#contact-container {
    top: 20px;
    right: 20px;
    position: fixed;
}

.contact-title {
    color: #222;
    background: none;
    border: none;
    font-family: 'Lora', serif;
    font-size: 16px;
    margin: 20px;
    text-align: center;
}

.contact-links {
    text-align: center;
}

.contact-icon {
    fill: #222;
    margin: 0 10px;
    -webkit-transition-duration: 0.2s;
         -o-transition-duration: 0.2s;
            transition-duration: 0.2s;
    width: 25px;
}

.contact-icon:hover {
    fill: #999;
    -webkit-transition-duration: 0.2s;
         -o-transition-duration: 0.2s;
            transition-duration: 0.2s;
}

@media screen and (max-width: 1000px) {
    #contact-container {
        position: static;
        margin-top: 20px;
    }

    @media screen and (max-width: 600px) {
        .contact-icon {
            width: 10%;
        }
    }
}


