#resume-container {
    display: -ms-flexbox;
    display: flex;
    margin: 0 auto;
    width: 50%;
    -webkit-animation: resume;
            animation: resume;
    -webkit-animation-duration: 0.3s;
            animation-duration: 0.3s;
}

@-webkit-keyframes resume {
    0% {height: 0px; display: none; color: #fff}
    90% {height: 550px; display: flex; color: #222}
    100% {height: initial; color: #222}
}

@keyframes resume {
    0% {height: 0px; display: none; color: #fff}
    90% {height: 550px; display: -ms-flexbox; display: flex; color: #222}
    100% {height: initial; color: #222}
}

.resume-left-side {
    margin: 0 20px 0 0;
    width: 35%;
}

.resume-right-side {
    margin: 0;
    width: 65%;
}

.resume-section-list {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 14px;
    font-weight: 300;
    list-style: none;
}

.resume-section-title {
    font-family: 'Lora', serif;
    font-size: 14px;
}

.resume-work-title {
    font-family: 'Lora', serif;
    font-size: 13px;
}

.resume-work-info {
    padding-left: 20px;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 14px;
    font-weight: 300;
}

@media screen and (max-width: 1200px) {
    #resume-container {
        -webkit-animation: none;
                animation: none;
        -ms-flex-direction: column;
            flex-direction: column;
        width: 60%;
        color: #000;
    }

    .resume-left-side {
        margin: 0 auto;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: justify;
            justify-content: space-between;
        width: 90%;
    }
    
    .resume-right-side {
        margin: 0 auto;
        width: 90%;
    }

    .resume-work-history {
        margin: 0 auto;
    }

    @media screen and (max-width: 600px){
        #resume-container {
            width: 90%;
        }

        .resume-work-title {
            font-size: 15px;
        }

        .resume-work-info {
            font-size: 14px;
        }
    }
}


